<template>
  <div id="app">
    <div
      :style="{width: `${pdfWidth}px`}"
      ref="pdfBox"
      class="pdf-box">
      <div
        class="pdf-elem"
        v-for="(item, index) in range"
        :key="index">
        <img v-lazy="`${url}${index + 1}.png`" :alt="`${url}${index + 1}.png`">
      </div>
    </div>
    <div class="scale-operate">
      <el-slider
      v-model="pdfWidth"
      vertical
      :min="min"
      :max="max"
      height="15vh"></el-slider>
    </div>
  </div>
</template>

<script>
export default {
  data() {
    return {
      range: 114,
      url: '/image/',
      pdfWidth: 0,
      scaleMultiply: 20,
      min: window.innerWidth * 0.5,
      max: window.innerWidth
    }
  },
  methods: {
    narrow() {
      this.pdfWidth -= this.scaleMultiply
      if (this.pdfWidth < this.min ) {
        this.pdfWidth = this.min
      }
    },
    increase() {
      this.pdfWidth += this.scaleMultiply
      if (this.pdfWidth > window.innerWidth ) {
        this.pdfWidth = window.innerWidth
      }
    }

  },
  mounted () {
    this.pdfWidth = this.min + (this.max - this.min) / 2
  },
}
</script>

